$principal: #AC6000;
$principal-light: #B27C00;
$secundario: #A50401;
$bg-brand: rgb(240, 215, 0);
$bg-light: #ECE1B5;

$dark: #111;
$texto: $dark;
$white: #fff;

$font-header: 'Poppins', sans-serif;
$font-body: 'Roboto', sans-serif;

html {
    font-size: 16px;
}

body {
	font-family: 'Poppins', sans-serif;
	color: $texto;
}

a {
    color: $principal;
    &:hover {
        text-decoration: none;
        color: $principal-light;
    }
}

button,input {
	outline:0;
}

.gt-main {
    background: url("../img/bg.jpg") no-repeat center top;
    color: $principal;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.gt-main__encabezado {
    background: $bg-brand !important;
    font-family: $font-header;
    font-size:1.2rem;
    letter-spacing: 1px;
    color: $principal;
    padding: .5rem 1rem;
    /*border-bottom: 2px solid $dark;*/

    .gt-page-title {
        flex: 1 1 auto;
        text-align: center;
        h3 {
            font-weight: 700;
            color: $principal;
            margin-bottom: 0px;
        }
    }
}

.gt-main__content {
    flex:1 1 auto;
    overflow: auto;
    width: 100%;
    padding: 16px 32px;
    margin:0 auto;
}

.paso {
    &.active {
        animation: scale-display .3s;
        display: block;
    }

    &.out {
        animation: scale-display--reversed .3s;
        animation-fill-mode:forwards;
        display: none;
    }
}

@keyframes scale-display {
  0% {
    display: none;
    opacity: 0;
  }

	100% {
		opacity: 1;
    display: block;

	}
}

@keyframes scale-display--reversed {
	0% {
		display: block;
		opacity: 1;
	}

	100% {
		display: none;
		opacity: 0;
	}
}

.gt-main__footer {
    padding: 16px 32px;
    background: $white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.gt-controls {
    button {
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-radius: 50%;
        margin-right: 24px;
        outline: 0;
        background: #ddd;
        color: $secundario;
        transition: all .35s ease-in-out;
        border: 0px;
        /*border: 2px solid #38424F;*/
        cursor: pointer;
        &:hover {
            transform: scale(1.1);
        }
    }
}

.sidenav {
    height: 100%; /* 100% Full-height */
    width: 0; /* 0 width - change this with JavaScript */
    position: fixed; /* Stay in place */
    z-index: 1; /* Stay on top */
    top: 0;
    left: 0;
    background-color: $white; /* Black*/
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 50px; /* Place content 60px from the top */
    transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */

    ul.sidenav-menu {
        padding-left: 0px;

        > li > a {
            font-family: $font-header;
            font-size: .9rem;
            color: $principal;
            font-weight: 300;
            &:hover{
                background: rgba(236,225,181,0.7);
            }
        }
    }
}

/* The navigation menu links */
.sidenav-menu a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    border-top: 1px solid #ddd; 
    transition: 0.3s;
}

/* Position and style the close button (top right corner) */
.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
    transition: all .35s ease-in-out;
    &:hover {
        transform: rotate(180deg);
    }
}

/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#main {
    transition: margin-left .5s;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
}

/* */
#gt-page-intermediacion {
    .container {
        max-width: 1260px;
    }
    .gt-beneficios-bloque {
        background: url("../img/engranaje.png") no-repeat center top;
        background-size: cover;
        width: 300px;
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 24px;
        position: relative;

        .inner {
            width: 140px;
        }

        .gt-campo {
            max-width: 150px;
        }

        ul {
            margin-top: 12px;
            li {
                list-style-type: none;
                color: $dark;
                text-align: center;
                font-size: 0.8rem;
            }
        }

        &:last-child {
            margin-right: 0px;
        }

        .flecha {
            position: absolute;
            width: 286px;
            height: 202px;
            top: -140px;
            left: -180px;
            background: url('../img/flecha.png') no-repeat top left;
        }
    }
}

.big-title {
    font-weight: 700;
    color: $secundario;
}

.flecha-bajar a {
    display: inline-block;
    color: $secundario;
    font-size: 4rem;
    animation: flecha-animada 2s ease-in-out infinite;
}

@keyframes flecha-animada {
    from {
        transform: translateY(0);
    }
    50% {
        transform: translateY(6px);
    }
    to {
        transform: translateY(0);
    }
}

.animar-boton {
    animation: boton-animado 2s ease-in-out infinite;
}

@keyframes boton-animado {
    from {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    to {
        transform: scale(1.2);
    }
}

/*************** Asesor Personalizado ******************/
#gt-page-asesor {

    .texto {
        flex: 1 1 60%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 100%;
    }
    .imagenes {
        flex: 1 1 40%;
        img {
            @media (min-width: 900px) {
                width: 240px;
            }
        }
    }
}

